import { ITransaction } from '../components/transaction';
import * as transactionService from '../components/transaction/transaction.service';

export async function customCreateTransaction(
  transaction: ITransaction,
  borrowerId: string,
  lenderId: string,
  session: any
) {
  const hola = await transactionService.createTransaction(
    transaction,
    borrowerId,
    lenderId,
    session
  );

  return hola
}
